export default {
  props: {
    message: {}
  },
  data() {
    return {
      // 消息详情是否显示 false；不显示，true:显示
      showFlag: false,
    }
  },
  methods: {
    // 显示消息详情
    show() {
      this.showFlag = true;
    },
    // 隐藏消息详情
    hide() {
      this.showFlag = false;
    },
  }
}



